
import { defineComponent } from "vue";
import { mapState } from "vuex";

export default defineComponent({
  props: [],
  computed: {
    ...mapState(["output", "error"]),
    isArray() {
      return !this.isError && Array.isArray(this.output);
    },
    isError() {
      return !!this.error;
    },
  },
});
